@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/* Nota: Estás usando la fuente "Nura", pero no la estás importando. 
   El navegador usará 'sans-serif' en su lugar. 
   Si 'Nura' es una fuente personalizada, asegúrate de importarla. */
body {
  font-family: "Nura", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-fondo: #070a0c;
  --color-orange: #e44528;
  --color-text: #fff;
  --color-text-dark: #343434;
  --color-azul: #1e3a8a;
  --color-blanco: #d1d0cf;
}

.container {
  background-color: var(--color-text-dark);
  max-height: fit-content;
  min-height: 90vh;
  position: relative; /* Añadido para que el contenido se ponga sobre el fondo */
  z-index: 1; /* Añadido para que el contenido se ponga sobre el fondo */
}


.blur {
  position: relative;
  z-index: 1;
  background: linear-gradient(135deg, #4D4D4D33, #4D4D4D33);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  cursor: pointer;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.82);
  height: fit-content;
  border-bottom-left-radius: 20px;
  border-top-left-radius:20px ;
}

.letterGlitchBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 90%;
  z-index: 0;
  pointer-events: none;
}



/* --- Estilos de la animación de fondo --- */
.background {
  position: absolute;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
        background: var(--color-orange);

    overflow: hidden;
    z-index: -2; /* Asegura que esté detrás de todo */
}

.background li {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    animation: animate 11s linear infinite;
        background: #070a0c;

}

/* --- CORRECCIÓN PRINCIPAL AQUÍ --- */
/* Este bloque @keyframes define la animación "animate" que faltaba */
@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}


