/* Resto de tus estilos... */

.cajauno {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.containerText {
  font-size: 1.2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  margin-left: 0.5rem;
}

.firstText,
.thirdText {
  color: var(--color-orange);
}

.skill {
  color: var(--color-text);
  font-size: 15px;
  margin-bottom: 1rem;
  margin-left: 0.5rem;
}

.skillsImg {
  margin: 1rem 0.5rem;
}

.skillsImg img {
  width: 25px;
  height: 25px;
}

.containerPersonal {
  background-image: linear-gradient(270deg, #7E2616 0%, #E44528 100%);
  border-radius: 13px;
  padding: 0.5rem;
  margin-right: 0.5rem;
  margin-left: 0.1rem;
  margin-top: 1rem;
}

.imgMai {
  width: 9rem;
  height: 6rem;
}

.secondText {
  color: var(--color-text);
}

.title {
  color: var(--color-text);
  font-size: 0.8rem;
  margin: 0.5rem 0;
}

.containerSkills {
  color: var(--color-text);
  font-size: 0.5rem;
  max-width: 160px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(5, auto);
  gap: 2px;
}

.containerSkills p {
  background-color: var(--color-text-dark);
  text-align: center;
  width: 100%;
  padding: 0.2rem 0;
  border-radius: 8px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.Work {
  background-image: linear-gradient(-270deg, #7E2616 0%, #E44528 100%);
  padding: 5px 10px;
  margin: 30px 10px 0 10px;
  border-top-right-radius: 5rem;
}

.titleclient {
  color: var(--color-text);
  font-size: 2rem;
  margin-bottom: 1rem;
}

.containerAll {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 1rem;
}

.info {
  display: flex;
  gap: 10px;
  color: var(--color-text);
  font-size: 0.5rem;
  background-color: var(--color-fondo);
  max-width: 320px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 10px;
}

.info img {
  width: 2rem;
  height: 2rem;
}

.infoTitleP {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.description {
  font-size: 0.4rem;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.ask p {
  font-size: 4rem;
  line-height: 3.5rem;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
  color: var(--color-text);
  text-align: end;
}


@media (min-width: 850px) {
  .blur {
    display: flex;
    justify-content: center;
    border-top-right-radius: 10rem;
    gap: 3rem;
  }

  .cajauno {
    justify-content: center;
    width: fit-content;
    gap: 20px;
  }

  .containerText {
    font-size: 2.5rem;
    width: 400px;
  }

  .skill {
    font-size: 25px;
  }

  .skillsImg img {
    width: 40px;
    height: 40px;
  }

  .containerPersonal {
    border-radius: 13px;
    padding: 1rem;
    height: fit-content;
    margin-top: 0.5rem;
  }

  .imgMai {
    width: 13rem;
    height: 9rem;
  }

  .title {
    font-size: 1rem;
  }

  .containerSkills {
    font-size: 0.8rem;
    max-width: 100%;
  }

  .titleclient {
    font-size: 2.5rem;
  }

  .Work {
    margin: 0 0 0 10px;
    width: 600px;
    border-top-right-radius: 10rem;
  }

  .ask {
    position: absolute;
    bottom: 10px;
    width: 42%;
    margin-right: auto;
  }

  .containerAll {
    position: relative;
  }

  .containerAll .info:nth-child(1) {
    max-width: 510px;
  }

  .containerAll .info:nth-child(2) {
    max-width: 500px;
  }

  .containerAll .info:nth-child(3) {
    max-width: 425px;
  }

  .containerAll .info:nth-child(4) {
    max-width: 370px;
  }

  .info {
    font-size: 0.5rem;
    max-width: 100%;
  }

  .info img {
    width: 4rem;
    height: 4rem;
  }

  .infoTitleP {
    font-size: 0.8rem;
  }

  .description {
    font-size: 0.6rem;
  }

  .container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #7E2616;
  }
}